\"~@ionic/angular/css/normalize.css\";\n@import \"~@ionic/angular/css/structure.css\";\n@import \"~@ionic/angular/css/typography.css\";\n@import '~@ionic/angular/css/display.css';\n\n/* Optional CSS utils that can be commented out */\n@import \"~@ionic/angular/css/padding.css\";\n@import \"~@ionic/angular/css/float-elements.css\";\n@import \"~@ionic/angular/css/text-alignment.css\";\n@import \"~@ionic/angular/css/text-transformation.css\";\n@import \"~@ionic/angular/css/flex-utils.css\";\n\nhtml, body {\n  box-sizing: border-box;\n  min-height: 100%;\n}\n*,\n*:before,\n*:after {\n  box-sizing: inherit;\n}\n\nion-grid {\n  --ion-grid-padding: 0px;\n}\n\n#logo {\n  height: 40px;\n}\n\nion-header {\n  background: #fff;\n  // box-shadow: 1px 1px 20px rgba(0,0,0,0.15);\n  color: #005191;\n  // height: 60px;\n}\n\nion-list {\n  --background: none;\n  padding: 20px;\n}\n\nion-content {\n  --background: white;\n}\n\n.full-page {\n  height: 80vh;\n}\n\n.white-background {\n  background: white;\n  padding: 20px;\n  border-radius: 5px;\n  margin: 20px;\n}\n\nion-toolbar {\n  --background: none;\n  --color: #005191;\n}\n\n.drawer-content {\n  padding-top: 40px;\n  background: rgba(0,0,0,0.05);\n  height: 50vh;\n\n  ion-list {\n    background: none;\n  }\n\n  ion-item{\n    --background: none;\n  }\n  ion-button {\n    margin: 10px auto;\n    padding: 5px;\n  }\n}\n\n.toolbar-title{\n  font-weight: 400;\n  font-size: 1.24em;\n  margin-left: 20px;\n}\n\nion-icon {\n  font-size: 2px;\n}\n\nion-title {\n  font-weight: 400;\n  font-size: 1.24em;\n  margin-left: 0px;\n}\n\n.post-content {\n  white-space: pre-wrap;\n  left: 6px;\n  font-size: 1.2em;\n  line-height: 25px;\n  width: 94%;\n}\n\n.comment-text, #mentor-description, #job-full-description, .reply-text, .event-description {\n  white-space: pre-wrap;\n}\n\n.searchbar-input {\n  background: #FFF !important;\n  border: 2px solid #005191 !important;\n  border-radius: 5px !important;\n  height: 40px !important;\n  padding: 10px 0px 10px 20px !important;\n  max-width: 100%;\n  font-size: 1.4em !important;\n  box-shadow: 1px 1px 10px rgba(0,0,0,0.1) !important;\n}\n\n.searchbar-input:hover {\n  border: 1px solid #005191 !important;\n  transition: 0.5s;\n}\n\n.searchbar-input:focus {\n  border: 1px solid #005191 !important;\n  transition: 0.5s;\n}\n\n@keyframes searchbar-slide-up {\n  0% {\n    transform: translateY(10px);\n  }\n\n  100% {\n    transform: translateY(0px);\n    opacity: 1;\n  }\n}\n\n\n.swiper-pagination {\n  // Swiper disabled before student app launch\n\n  position: relative;\n  bottom: 20px !important;\n  left: 45%;\n  top: -90px;\n  bottom: calc(env(safe-area-inset-bottom) + 40px) !important;\n  background: #eee;\n  border-radius: 50px;\n  padding: 5px;\n  width: 100px !important;\n  margin: 0 auto !important;\n  }\n\n  .swiper-pagination-bullet {\n    background: #333;\n}\n  .swiper-pagination-bullet-active {\n    background: #005191;\n}\n\n.disabled {\n  opacity: 1;\n}\n\nion-input:focus {\n  padding: 10px 0;\n}\n\n.validation-errors{\n  background: none;\n}\n\n.line-input {\n  background: none;\n\n  ion-item {\n    // --background: white;\n      --border-color: transparent!important;\n      --highlight-height: 0;\n      height: 40px;\n      margin: 8px auto;\n      border: 2px solid #dedede;\n      transition: 0.2s;\n      border-radius: 4px;\n      opacity: 1;\n      color: #999;\n      &.item-has-focus {\n          padding: 6px 0;\n          border: 2px solid var(--ion-color-primary);\n          //  background: rgba(255,179,81,0.1);\n          opacity: 1;\n          color: #005191;\n          font-size: 1.1em;\n          font-weight: 500;\n          letter-spacing: 0.05em;\n          box-shadow: 1px 1px 10px rgba(0,0,0,0.1)\n      }\n      ion-label {\n        opacity: 1;\n        color: #005191;\n        word-wrap: break-word;\n        overflow-wrap: break-word;\n        display: inline;\n      }\n  }\n}\n\nion-select {\n  /* Applies to the value and placeholder color 